import React from "react";
import {isEmpty, isArray} from "lodash";
import PropTypes from "prop-types";
import ImgGallery from "./ImgGallery";
import ReactPlayer from "react-player";
import {
	SideBySideMagnifier,
	MOUSE_ACTIVATION,
	TOUCH_ACTIVATION,
} from "react-image-magnifiers";

const MediaPart = (props) => {
	const {product, activeImg} = props;
	let Videos = product.Videos;
	Videos = isArray(Videos) ? Videos[0] : {};
	const PreviewUrl = !isEmpty(Videos) ? Videos.PreviewUrl : "";
	const Url = Videos?.Url;


	return (
		<div className="product-gallery product-gallery-vertical">
			<figure className="product-main-image">
				{activeImg === Url && Url ? (
					<ReactPlayer
						muted={true}
						playing={true}
						controls={true}
						playsinline={true}
						loop={true}
						width="100%"
						height="auto"
						url={Url}
					/>
				) : (
					<SideBySideMagnifier
						imageSrc={activeImg ? activeImg : product.MainPictureUrl}
						imageAlt={product.Title}
						// onImageLoad={true}
						mouseActivation={MOUSE_ACTIVATION.click} // Optional
						touchActivation={TOUCH_ACTIVATION.TAP} // Optional
						fillAvailableSpace={false} // if false fillGapLeft not working
						// fillGapLeft={15}
						// fillGapRight={8}
						// fillGapTop={12}
						// fillGapBottom={10}
					/>
				)}
			</figure>
			<ImgGallery
				PreviewUrl={PreviewUrl}
				Url={Url}
				setActiveImg={props.setActiveImg}
				activeImg={activeImg}
				Pictures={product.Pictures}
			/>
		</div>
	);
};

MediaPart.propTypes = {
	product: PropTypes.object.isRequired,
};

export default MediaPart;
